
export function isArray(obj) {
    return Array.isArray(obj);
}

export function isObject(obj) {
    return Object.prototype.toString.call(obj) === '[object Object]';
}

export function parseQuery(search) {
    return search.slice(1).split('&').map(function (seg) {
        return seg.split('=');
    }).map(function (pair) {
        return pair.map(decodeURIComponent);
    }).reduce(function (map, pair) {
        return map[pair[0]] = pair[1], map;
    }, {});
}

import { findFourSelect } from '../lib/api';
import { COMMODITY_CODE } from '../lib/constants';
export function linkageLevelData(level, id) {
    var levelModel = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : COMMODITY_CODE.divLevel;

    if (id) {
        return findFourSelect(level, id).then(function (r) {
            if (r.status === 200) {
                switch (level) {
                    case 1:
                        levelModel.div = r.returnList;
                        levelModel.dep = [];
                        levelModel.class = [];
                        levelModel.subclass = [];
                        break;
                    case 2:
                        levelModel.dep = r.returnList;
                        levelModel.class = [];
                        levelModel.subclass = [];
                        break;
                    case 3:
                        levelModel.class = r.returnList;
                        levelModel.subclass = [];
                        break;
                    case 4:
                        levelModel.subclass = r.returnList;
                        break;
                    default:
                        console.log('');
                }
            } else {
                levelModel.dep = [];
                levelModel.class = [];
                levelModel.subclass = [];
                //window.alert(r.msg || '');
            }
        }).catch(function () {
            levelModel.dep = [];
            levelModel.class = [];
            levelModel.subclass = [];
            //window.alert(r.msg || '');
        });
    }
}

/**
 * code to 
 * @param listKey key
 * @param status 
 */
export function codeToLabel(listKey, status) {
    var item = COMMODITY_CODE[listKey].filter(function (i) {
        return i.value === status;
    });
    if (item[0]) {
        return item[0].label;
    }
}


